<template>
  <div class="container my-2">
    <h1>Ruta Empleado y Administrador!</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>

    <!--Formulario editar juego-->
    <form @submit.prevent="editarJuego(gameEditar)" v-if="editar">
      <h3>Editar Juego</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre Juego"
        v-model="gameEditar.nombre_juego"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre empresa desarrolladora"
        v-model="gameEditar.desarrollador_juego"
      />
      <input
        type="number"
        class="form-control my-2"
        placeholder="Precio del juego "
        v-model="gameEditar.precio"
        required
      />
      <b-form-group label="Sistema PEGI">
        <b-form-radio v-model="gameEditar.PEGI" name="PEGI_3 " value="3">PEGI 3</b-form-radio>
        <b-form-radio v-model="gameEditar.PEGI" name="PEGI_7 " value="7">PEGI 7</b-form-radio>
        <b-form-radio v-model="gameEditar.PEGI" name="PEGI_12" value="12">PEGI 12</b-form-radio>
        <b-form-radio v-model="gameEditar.PEGI" name="PEGI_16" value="16">PEGI 16</b-form-radio>
        <b-form-radio v-model="gameEditar.PEGI" name="PEGI_18" value="18">PEGI 18</b-form-radio>
      </b-form-group>
      <!-- ChechBox para generos-->
      <b-form-group md="3" class="my-2 mx-2" label="Genero(s) del juego:">
        <b-form-checkbox-group v-model="gameEditar.genero" :options="genres"></b-form-checkbox-group>
      </b-form-group>

      <b-button class="btn-warning mx-2 my-2" type="submit">Finalizar edicion</b-button>
      <b-button class="btn-danger my-2" @click="editar=false">Cancelar edicion</b-button>
    </form>

    <!--Formulario de crear juego-->
    <form @submit="agregarGame()" v-if="!editar">
      <h3>Agregar Juego</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre Juego"
        v-model="newGame.nombre_juego"
        required
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre empresa desarrolladora"
        v-model="newGame.desarrollador_juego"
        required
      />
      <input
        type="number"
        class="form-control my-2"
        placeholder="Precio del juego "
        v-model="newGame.precio"
        required
      />
      <!--Input de fechaSalida-->
      <b-container>
        <b-row class="my-2 mx-2" v-for="type in types" :key="type">
          <b-col sm="2">
            <label :for="`type-${type}`">Fecha Salida:</label>
          </b-col>
          <b-col sm="9">
            <b-form-input :type="type" v-model="newGame.fechaSalida"></b-form-input>
          </b-col>
        </b-row>
      </b-container>
      <b-form-group label="Sistema PEGI">
        <b-form-radio v-model="newGame.PEGI" name="PEGI_3 " value="3">PEGI 3</b-form-radio>
        <b-form-radio v-model="newGame.PEGI" name="PEGI_7 " value="7">PEGI 7</b-form-radio>
        <b-form-radio v-model="newGame.PEGI" name="PEGI_12" value="12">PEGI 12</b-form-radio>
        <b-form-radio v-model="newGame.PEGI" name="PEGI_16" value="16">PEGI 16</b-form-radio>
        <b-form-radio v-model="newGame.PEGI" name="PEGI_18" value="18">PEGI 18</b-form-radio>
      </b-form-group>
      <!-- ChechBox para generos-->
      <b-form-group md="3" class="my-2 mx-2" label="Genero(s) del juego:">
        <b-form-checkbox-group v-model="newGame.genero" :options="genres"></b-form-checkbox-group>
      </b-form-group>

      <b-button class="btn-success my-2" type="submit">Agregar Juego</b-button>
    </form>

    <!--Mostrar juegos de la DB-->
    <table class="table">
      <thead>
        <tr>
          <th scope="col">Nombre Juego</th>
          <th scope="col">Desarrollador</th>
          <th scope="col">Genero(s)</th>
          <th scope="col">PEGI</th>
          <th scope="col">Fecha de salida</th>
          <th scope="col">Precio</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in game" :key="index">
          <td>{{item.nombre_juego}}</td>
          <td>{{item.desarrollador_juego}}</td>
          <td>{{item.genero}}</td>
          <td>{{item.PEGI}}</td>
          <td>{{item.fechaSalida}}</td>
          <td>{{item.precio}}</td>
          <td>
            <b-button @click="eliminarGame(item._id)" class="btn-danger btn-sm mx-2">Eliminar</b-button>
            <b-button @click="activarFormEdicion(item._id)" class="btn-warning btn-sm">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      game: [],
      dismissSecs: 3,
      dismissCountDown: 0,
      mensaje: { color: "", texto: "" },
      newGame: {
        nombre_juego: "",
        desarrollador_juego: "",
        genero: [],
        PEGI: "",
        fechaSalida: "",
        precio: "",
      },
      types: ["date"],
      genres: [
        { text: "Acción", value: "Acción" },
        { text: "Lucha", value: "Lucha" },
        { text: "Arcade", value: "Arcade" },
        {
          text: "Disparos en primera persona",
          value: "Disparos en primera persona",
        },
        {
          text: "Disparos en tercera persona",
          value: "Disparos en tercera persona",
        },
        { text: "Estrategia", value: "Estrategia" },
        { text: "Deportes", value: "Deportes" },
        { text: "Carrera", value: "Carrera" },
        { text: "Aventura", value: "Aventura" },
        { text: "Survival Horror", value: "Survival Horror" },
        { text: "RPG", value: "RPG" },
        { text: "Battle Royale", value: "Battle Royale" },
        { text: "Sandbox", value: "Sandbox" },
        { text: "Educación", value: "Educación" },
        { text: "Puzzle", value: "Puzzle" },
        { text: "Misterio", value: "Misterio" },
      ],
      gameEditar: {},
      editar: false,
    };
  },
  created() {
    this.listarGame();
  },
  methods: {
    listarGame() {
      this.axios
        .get("/game")
        .then((res) => {
          this.game = res.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    //Crear juego
    agregarGame() {
      this.axios
        .post("/nuevoGame", this.newGame)
        .then((res) => {
          this.game.push(res.data);
          (this.game.nombre_juego = ""),
            (this.game.desarrollador_juego = ""),
            (this.game.genero = [""]),
            (this.game.PEGI = ""),
            (this.game.fechaSalida = ""),
            (this.game.precio = ""),
            (this.mensaje.color = "success"),
            (this.mensaje.texto = "Juego agregado satisfactoriamente!"),
            this.showAlert();
            console.log(this.game)
        })
        .catch((e) => {
          console.log(e);
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = "Ops algo salio mal!!"),
            this.showAlert();
        });
    },

    eliminarGame(id) {
      this.axios
        .delete(`/game/${id}`)
        .then((res) => {
          const index = this.game.findIndex(
            (item) => item._id === res.data._id
          );
          this.game.splice(index, 1);
          (this.mensaje.color = "warning"),
            (this.mensaje.texto = "Juego eliminado"),
            this.showAlert();
        })
        .catch((e) => {
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = "Ops algo salio mal!!"),
            this.showAlert();
          console.log(e);
        });
    },
    activarFormEdicion(id) {
      this.editar = true;
      this.axios
        .get(`/game/${id}`)
        .then((res) => {
          this.gameEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    editarJuego(item) {
      this.axios
        .put(`/game/${item._id}`, item)
        .then((res) => {
          const index = this.game.findIndex((n) => n._id === res.data._id);
          this.game[index].nombre_juego = res.data.nombre_juego;
          this.game[index].desarrollador_juego = res.data.desarrollador_juego;
          this.game[index].PEGI = res.data.PEGI;
          this.game[index].fechaSalida = res.data.fechaSalida;
          this.game[index].genero = res.data.genero;
          this.game[index].precio = res.data.precio;
          (this.mensaje.color = "primary"),
            (this.mensaje.texto = "Juego Actualizado"),
            this.showAlert();
          this.editar = false;
        })
        .catch((e) => {
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = "Ops algo salio mal!"),
            this.showAlert();
          this.editar = false;
          console.log(e.response);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
};
</script>